import React, { Suspense } from 'react';
import { Route, Routes } from 'react-router-dom';
import Loader from './component/loader';

const HomeIndex = React.lazy(() => import('./pages/home/index'));
const BackTop = React.lazy(() => import('./component/back-top'));
const Header1 = React.lazy(() => import('./layouts/header/Header1'));
const FooterStyle01 = React.lazy(() =>
  import('./layouts/footer/footer-style-01')
);
const Contact = React.lazy(() =>
  import('./pages/contact-us/index')
);

const About = React.lazy(() =>
  import('./pages/about-us/index')
);

const Service = React.lazy(() => import('./pages/services/index'));
const ServicesSingle = React.lazy(() =>
  import('./pages/services/services-details')
);

const Deals = React.lazy(() =>
  import('./pages/deals/index')
);

const Cases = React.lazy(() =>
  import('./pages/cases/case-study')
);
const CaseSingle = React.lazy(() =>
  import('./pages/cases/case-study-details')
);

const Industries = React.lazy(() =>
  import('./pages/industries/index')
);

const Industries2 = React.lazy(() =>
  import('./pages/industries/industries-2')
);

const Industries3 = React.lazy(() =>
  import('./pages/industries/industries-3')
);

const Industries4 = React.lazy(() =>
  import('./pages/industries/industries-4')
);

const MissionVision = React.lazy(() =>
  import('./pages/mission-vision/index')
);

const OurValue = React.lazy(() =>
  import('./pages/our-value/index')
);

const Faq = React.lazy(() =>
  import('./pages/faq/faq')
);

const PrivacyPolicy = React.lazy(() =>
  import('./pages/privacy-policy/index')
);

const TermsConditions = React.lazy(() =>
  import('./pages/terms-conditions/index')
);
const HowWeWork = React.lazy(() =>
  import('./pages/how-we-work/index')
);
const Error404 = React.lazy(() =>
  import('./pages/error404/error404')
);

const ComingSoon = React.lazy(() =>
  import('./pages/coming-soon/index')
);

function App() {
  return (
    <Suspense fallback={<Loader />}>
      <div className="main-wrapper">
        <Routes>
        <Route path="/" element={<Header1 />}>
          <Route index path='/' element={<HomeIndex />} />
          <Route path='/contact' element={<Contact />} />
          <Route path='/about-us' element={<About />} />
          <Route path="/services" element={<Service />} />
          <Route path="/service-details/:id" element={<ServicesSingle />} />
          <Route path="/deals" element={<Deals />}/>
          <Route path="/case-study" element={<Cases />} />
          <Route path="/case-study-details/:id" element={<CaseSingle />} />
          <Route path="/industries" element={<Industries />} />
          <Route path="/industries-2" element={<Industries2 />} />
          <Route path="/industries-3" element={<Industries3 />} />
          <Route path="/industries-4" element={<Industries4 />} />   
          <Route path="/mission-vision" element={<MissionVision />} />
          <Route path="/our-value" element={<OurValue />} />      
          {
            /*
                      <Route path="/*" element={<Error404 />} />
                                  <Route path="/*" element={<ComingSoon />} />


            */
          }
            <Route path="/privacy-policy" element={<PrivacyPolicy />} />
            <Route path="/terms-and-conditions" element={<TermsConditions />} />
            <Route path="/how-we-work" element={<HowWeWork />} />
            <Route path="/faq" element={<Faq />} />
            <Route path="/*" element={<Error404 />} />




        </Route>
        </Routes>

        <Routes>
        <Route path="/*" element={<FooterStyle01 />} />{' '}
        </Routes>
        <BackTop />

      </div>
      </Suspense>
  );
}

export default App;
